export const query = graphql`
  query($slug: String) {
    coursesYaml(basic_info: { slug: { eq: $slug } }) {
      info
      basic_info {
        slug
      }
      banner {
        headertext
        title
        time
      }
      description {
        description
        time
        menu {
            title
            detail {
              title
              text
            }
        }
      }
      course_structure{
        menu_structure{
          title
          detail{
            title
          }
        }
      }
      training{
        title
        subtitle
        description{
          trainer
          description
          img{
            childImageSharp {
              fluid(maxWidth: 350) {
                ...GatsbyImageSharpFluid_withWebp
              }
            }
          }
        }
      }
    }
  }
`;
